body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*App,js*/
.app-body {
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 3em;
  background: #11998e; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #38ef7d,
    #11998e
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.app-grid {
  width: 80%;
  height: 90%;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  margin: auto;
  -webkit-overflow-scrolling: touch;
  overflow: auto;
}

.app-title-cell {
  background: url(/react-weather-app/static/media/Mountains.065caea3.jpg) center center no-repeat;
  background-size: cover;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  color: white;
}
.app-weather-cell {
  /*background: rgb(51, 51, 51);*/
  background: white;
}
/*title.js*/
.title-body h1 {
  color: white;
  font-family: "Fjalla One", sans-serif;
}
@media (max-width: 600px) {
  .title-body h1 {
    color: white;
    font-size: 33px;
    font-family: "Fjalla One", sans-serif;
  }
  .title-body h5 {
    font-family: "Alegreya Sans", sans-serif;
    font-size: 15px;
  }
}

.title-body h5 {
  font-family: "Alegreya Sans", sans-serif;
}

.form-body {
  margin: auto;
  padding: 3em;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.weather-body {
  text-align: left;
  margin: auto;
  padding-left: 3em;
  font-family: "Alegreya Sans", sans-serif;
}
.weather-body p {
  color: blue;
  font-size: 30px;
}
.weather-body hr {
  color: grey;
  width: 50%;
  margin-top: 3em;
  margin-bottom: 3em;
}
.weather-body-span {
  color: black;
}

@media (max-width: 1000px) {
  .weather-body {
    text-align: left;
    margin: auto;
    padding-left: 3em;
    font-family: "Alegreya Sans", sans-serif;
  }
  .weather-body p {
    color: blue;
    font-size: 20px;
  }
  .weather-body hr {
    color: grey;
    width: 50%;
    margin-top: 1em;
    margin-bottom: 1em;
  }
  .weather-body-span {
    color: black;
  }
}

